
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import selectCity from '@/components/SelectCity'
import {mapGetters} from 'vuex'
export default {
  name: 'customerAdd',
  components: {
    Group,
    Cell,
    XInput,
    XButton,
    XTextarea,
    selectCity,
    Datetime
  },
  data () {
    return {
      form: {
        userId: '',
        customer: {
          name: '',
          phone: '',
          areaFullName: '',
          carNo: '',
          vin: '',
          engine: '',
          registTime: '',
          expireTime: '',
          note: '',
          driverCard: '',
          idCard: '',
          areaId: '',
          subDriverCard: ''
        }
      }
    }
  },
  created () {
    this.form.userId = this.$route.params.userId
  },
  computed: {
    ...mapGetters({
      areaId: 'getInsuranceArea'
    })
  },
  methods: {
    submit () {
      this.form.customer.areaId = this.areaId
      this.form.customer = JSON.stringify(this.form.customer)
      // console.log(this.form.customer)
      this.$http({
        method: 'jsonp',
        url: 'http://liuwbox.com/zzbao/app/customer/submit.htm',
        jsonp: 'callback',
        params: this.form,
        jsonpCallback: 'json'
      })
      .then(res => {
        console.log(res)
        if (res.body.status) {
          this.$vux.toast.show({
            type: 'text',
            width: '15em',
            position: 'bottom',
            text: '',
            time: '3000'
          })
        }
      })
    }
  }
}



